<template><markdown-wrapper><h1 id="popover">Popover <a class="header-anchor" href="#popover" aria-hidden="true">&#128279;</a></h1>
<blockquote>
<p>Add small overlays of content, like those on the iPad, to any element for housing secondary information.</p>
</blockquote>
<h2 id="example">Example <a class="header-anchor" href="#example" aria-hidden="true">&#128279;</a></h2>
<p><strong>Note</strong>: Popovers whose both title and content are zero-length are never displayed.</p>
<p>Click the button below to toggle popover:</p>
<popover-example ref="popover-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"btn"</span>&gt;</span>Popover<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">popover</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Title"</span> <span class="hljs-attr">target</span>=<span class="hljs-string">"#btn"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"popover"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">h1</span>&gt;</span>Hello world!<span class="hljs-tag">&lt;/<span class="hljs-name">h1</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">popover</span>&gt;</span>
<span class="hljs-comment">&lt;!-- popover-example.vue --&gt;</span>
</code></pre>
<h3 id="trigger-target">Trigger target <a class="header-anchor" href="#trigger-target" aria-hidden="true">&#128279;</a></h3>
<p>Order to decide the popover trigger:</p>
<ol>
<li>Use <code v-pre>target</code> if exist.</li>
<li>Use element in default slot with <code v-pre>data-role=&quot;trigger&quot;</code> attribute if exist.</li>
<li>Use the first element present in default slot.</li>
</ol>
<p>A <code v-pre>target</code> can be:</p>
<ul>
<li>Selector that can be recognized by <code v-pre>querySelect</code>.</li>
<li>Reference to Element.</li>
<li>Reference to Component.</li>
</ul>
<h2 id="directive">Directive <a class="header-anchor" href="#directive" aria-hidden="true">&#128279;</a></h2>
<p>You can also simply use popovers via <code v-pre>v-popover</code> directive:</p>
<popover-directive ref="popover-directive"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover</span>=<span class="hljs-string">"{title:'Title', content:'Popover content'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Popover<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- popover-directive.vue --&gt;</span>
</code></pre>
<h2 id="with-empty-title">With empty title <a class="header-anchor" href="#with-empty-title" aria-hidden="true">&#128279;</a></h2>
<p>If you don't want the title of popover, just leave the <code v-pre>title</code> prop unset or blank.</p>
<popover-with-empty-title ref="popover-with-empty-title"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover</span>=<span class="hljs-string">"{content:'Popover without a title'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Popover<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- popover-with-empty-title.vue --&gt;</span>
</code></pre>
<h2 id="placements">Placements <a class="header-anchor" href="#placements" aria-hidden="true">&#128279;</a></h2>
<p>Supported placements:</p>
<ul>
<li><strong>top</strong> (Default)</li>
<li><strong>right</strong></li>
<li><strong>bottom</strong></li>
<li><strong>left</strong></li>
</ul>
<popover-placements ref="popover-placements"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover.left</span>=<span class="hljs-string">"{title:'Title', content:'Popover on left'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Left<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover.top</span>=<span class="hljs-string">"{title:'Title', content:'Popover on top'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Top<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover.bottom</span>=<span class="hljs-string">"{title:'Title', content:'Popover on bottom'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Bottom<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover.right</span>=<span class="hljs-string">"{title:'Title', content:'Popover on right'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Right<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- popover-placements.vue --&gt;</span>
</code></pre>
<h2 id="auto-placement">Auto placement <a class="header-anchor" href="#auto-placement" aria-hidden="true">&#128279;</a></h2>
<p>Popover will try to find the best placement for displaying while <code v-pre>auto-placement</code> is set to <code v-pre>true</code> (by default) base on the default placement setting. Useful while there does not have enough space to show the entire popover content.</p>
<p><code v-pre>auto-placement</code> try order: right -&gt; bottom -&gt; left -&gt; top, and use the set one if none of these matched.</p>
<h2 id="triggers">Triggers <a class="header-anchor" href="#triggers" aria-hidden="true">&#128279;</a></h2>
<p>Supported triggers:</p>
<ul>
<li><code v-pre>hover</code> show on mouseenter, hide on mouseleave.</li>
<li><code v-pre>focus</code> show on focus, hide on blur.</li>
<li><code v-pre>hover-focus</code> combination of hover and focus trigger.</li>
<li><code v-pre>click</code> toggle on trigger click.</li>
<li><code v-pre>outside-click</code> (Default) same as click, but not close on popover click and close on outside click.</li>
</ul>
<popover-triggers ref="popover-triggers"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover</span>=<span class="hljs-string">"{title:'Title', content:'Popover content'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Outside-Click (Default)<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover.hover</span>=<span class="hljs-string">"{title:'Title', content:'Popover content'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Hover<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover.focus</span>=<span class="hljs-string">"{title:'Title', content:'Popover content'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Focus<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover.hover-focus</span>=<span class="hljs-string">"{title:'Title', content:'Popover content'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Hover-Focus<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover.click</span>=<span class="hljs-string">"{title:'Title', content:'Popover content'}"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Click<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- popover-triggers.vue --&gt;</span>
</code></pre>
<h2 id="manual-trigger">Manual trigger <a class="header-anchor" href="#manual-trigger" aria-hidden="true">&#128279;</a></h2>
<p>Set <code v-pre>trigger</code> prop to <code v-pre>manual</code> to disable all the event listeners, and controls popover show / hide only by <code v-pre>v-model</code> change.</p>
<popover-manual-trigger ref="popover-manual-trigger"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">popover</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Title"</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"manual"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"show"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>You Can't Trigger Popover Here...<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"popover"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Popover content<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">popover</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">hr</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show = !show"</span>&gt;</span>Toggle Popover<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- popover-manual-trigger.vue --&gt;</span>
</code></pre>
<h2 id="disable-popover">Disable popover <a class="header-anchor" href="#disable-popover" aria-hidden="true">&#128279;</a></h2>
<p>Set <code v-pre>enable</code> prop to <code v-pre>false</code> to disable a popover.</p>
<popover-disable ref="popover-disable"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">popover</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Title"</span> <span class="hljs-attr">:enable</span>=<span class="hljs-string">"false"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Disabled Popover<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"popover"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">h1</span>&gt;</span>Hello world!<span class="hljs-tag">&lt;/<span class="hljs-name">h1</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">popover</span>&gt;</span>
<span class="hljs-comment">&lt;!-- popover-disable.vue --&gt;</span>
</code></pre>
<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="popover-2"><a href="https://github.com/wxsms/uiv/blob/master/src/components/popover/Popover.vue">Popover</a> <a class="header-anchor" href="#popover-2" aria-hidden="true">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Boolean</td>
<td></td>
<td></td>
<td>Show / hide the popover.</td>
</tr>
<tr>
<td><code v-pre>target</code></td>
<td></td>
<td></td>
<td></td>
<td>Popover trigger, can be a select or reference to Element / Component.</td>
</tr>
<tr>
<td><code v-pre>tag</code></td>
<td>String</td>
<td>span</td>
<td></td>
<td>The HTML tag that render the component.</td>
</tr>
<tr>
<td><code v-pre>title</code></td>
<td>String</td>
<td></td>
<td></td>
<td>The popover title.</td>
</tr>
<tr>
<td><code v-pre>content</code></td>
<td>String</td>
<td></td>
<td></td>
<td>The popover content text. Use <code v-pre>popover</code> slot instead if you need full control.</td>
</tr>
<tr>
<td><code v-pre>enable</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Enable the popover.</td>
</tr>
<tr>
<td><code v-pre>enterable</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Whether mouse can enter the popover.</td>
</tr>
<tr>
<td><code v-pre>placement</code></td>
<td>String</td>
<td>top</td>
<td></td>
<td>The popover placement, support <code v-pre>top</code> / <code v-pre>bottom</code> / <code v-pre>left</code> / <code v-pre>right</code>.</td>
</tr>
<tr>
<td><code v-pre>auto-placement</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Try to auto adjust the placement if the set one does not have enough space to show.</td>
</tr>
<tr>
<td><code v-pre>trigger</code></td>
<td>String</td>
<td>outside-click</td>
<td></td>
<td>The popover trigger event, support <code v-pre>hover</code> / <code v-pre>focus</code> / <code v-pre>hover-focus</code> / <code v-pre>click</code> / <code v-pre>outside-click</code> / <code v-pre>manual</code></td>
</tr>
<tr>
<td><code v-pre>append-to</code></td>
<td>String</td>
<td>body</td>
<td></td>
<td>Element selector that the popover append to.</td>
</tr>
<tr>
<td><code v-pre>transition-duration</code></td>
<td>Number</td>
<td>150</td>
<td></td>
<td>The popover show / hide transition time in ms.</td>
</tr>
</tbody>
</table></div><h3 id="slots">Slots <a class="header-anchor" href="#slots" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>popover</code></td>
<td>Replace as the popover body.</td>
</tr>
<tr>
<td><code v-pre>default</code></td>
<td>Replace as the rest of the component (e.g. trigger stuffs).</td>
</tr>
</tbody>
</table></div><h3 id="events">Events <a class="header-anchor" href="#events" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Params</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>show</code></td>
<td></td>
<td>Fire after popover show.</td>
</tr>
<tr>
<td><code v-pre>hide</code></td>
<td></td>
<td>Fire after popover hide.</td>
</tr>
</tbody>
</table></div><h3 id="directive-2"><a href="https://github.com/wxsms/uiv/blob/master/src/directives/popover/popover.js">Directive</a> <a class="header-anchor" href="#directive-2" aria-hidden="true">&#128279;</a></h3>
<p>The binding value will be the title and text content of corresponding popover.</p>
<p><strong>Simplest Usage</strong></p>
<pre v-pre><code v-pre>v-popover=&quot;{title:'Title', content:'Popover content'}&quot;
</code></pre>
<p><strong>Placements Examples</strong></p>
<pre v-pre><code v-pre>v-popover.left=&quot;{title:'Title', content:'Popover content'}&quot;
v-popover.right=&quot;{title:'Title', content:'Popover content'}&quot;
</code></pre>
<p><strong>Triggers Examples</strong></p>
<pre v-pre><code v-pre>v-popover.hover=&quot;{title:'Title', content:'Popover content'}&quot;
v-popover.click=&quot;{title:'Title', content:'Popover content'}&quot;
</code></pre>
<p><strong>Unenterable</strong></p>
<pre v-pre><code v-pre>v-popover.unenterable=&quot;{title:'Title', content:'Popover content'}&quot;
</code></pre>
<p><strong>Custom append-to</strong></p>
<pre v-pre><code v-pre>v-popover:arg=&quot;{title:'Title', content:'Popover content'}&quot;
</code></pre>
<p><code v-pre>arg</code> is the ID (without prefix <code v-pre>#</code>) of the element to append to, leave it empty to use default value <code v-pre>body</code>.</p>
<p><strong>Combination</strong></p>
<pre v-pre><code v-pre>v-popover.left.hover=&quot;{title:'Title', content:'Popover content'}&quot;
v-popover:some-id.right.click=&quot;{title:'Title', content:'Popover content'}&quot;
</code></pre>
</markdown-wrapper></template><script>
  
export default {components:{'popover-example':{"template":"<div class=\"markdown-live-example\"><btn type=\"primary\" id=\"btn\">Popover</btn>\n<popover title=\"Title\" target=\"#btn\">\n  <template slot=\"popover\">\n    <h1>Hello world!</h1>\n  </template>\n</popover>\n<!-- popover-example.vue --></div>"},'popover-directive':{"template":"<div class=\"markdown-live-example\"><btn v-popover=\"{title:'Title', content:'Popover content'}\" type=\"primary\">Popover</btn>\n<!-- popover-directive.vue --></div>"},'popover-with-empty-title':{"template":"<div class=\"markdown-live-example\"><btn v-popover=\"{content:'Popover without a title'}\" type=\"primary\">Popover</btn>\n<!-- popover-with-empty-title.vue --></div>"},'popover-placements':{"template":"<div class=\"markdown-live-example\"><btn v-popover.left=\"{title:'Title', content:'Popover on left'}\" type=\"primary\">Left</btn>\n<btn v-popover.top=\"{title:'Title', content:'Popover on top'}\" type=\"primary\">Top</btn>\n<btn v-popover.bottom=\"{title:'Title', content:'Popover on bottom'}\" type=\"primary\">Bottom</btn>\n<btn v-popover.right=\"{title:'Title', content:'Popover on right'}\" type=\"primary\">Right</btn>\n<!-- popover-placements.vue --></div>"},'popover-triggers':{"template":"<div class=\"markdown-live-example\"><btn v-popover=\"{title:'Title', content:'Popover content'}\" type=\"primary\">Outside-Click (Default)</btn>\n<btn v-popover.hover=\"{title:'Title', content:'Popover content'}\" type=\"primary\">Hover</btn>\n<btn v-popover.focus=\"{title:'Title', content:'Popover content'}\" type=\"primary\">Focus</btn>\n<btn v-popover.hover-focus=\"{title:'Title', content:'Popover content'}\" type=\"primary\">Hover-Focus</btn>\n<btn v-popover.click=\"{title:'Title', content:'Popover content'}\" type=\"primary\">Click</btn>\n<!-- popover-triggers.vue --></div>"},'popover-manual-trigger':{"template":"<div class=\"markdown-live-example\">\n  <popover title=\"Title\" trigger=\"manual\" v-model=\"show\">\n    <btn>You Can't Trigger Popover Here...</btn>\n    <template slot=\"popover\">\n      <p>Popover content</p>\n    </template>\n  </popover>\n  <hr/>\n  <btn type=\"primary\" @click=\"show = !show\">Toggle Popover</btn>\n</div>",
    data () {
      return {
        show: false
      }
    }
  },'popover-disable':{"template":"<div class=\"markdown-live-example\"><popover title=\"Title\" :enable=\"false\">\n  <btn type=\"primary\">Disabled Popover</btn>\n  <template slot=\"popover\">\n    <h1>Hello world!</h1>\n  </template>\n</popover>\n<!-- popover-disable.vue --></div>"}}}</script>


